body{padding-top:100px;font-family:"Roboto",sans-serif;font-size:14px;max-width:900px;margin:auto;background:linear-gradient(180deg, rgba(150, 143, 216, 0.21) 0%, #815692 99%),url(/languageapp/static/media/fon-iz-garri-pottera.bff59d12.png) no-repeat center top/cover}body *{box-sizing:border-box}
.App{max-width:1170px;min-height:100vh;margin:auto;width:90%;display:flex;flex-direction:column}.main{display:flex;flex-direction:column;align-items:center;flex:1 1 auto}
.header{position:fixed;top:0;left:0;width:100%;height:70px;padding-top:10px;padding-bottom:30px;font-family:"Roboto",sans-serif;font-size:14px;display:flex;flex-direction:row;justify-content:space-evenly;background:#473995}@media screen and (max-width: 720px){.header{flex-direction:column;align-items:center;padding-top:0;justify-content:flex-start;height:90px;font-size:10px}}.logo{width:70px;padding:10px}.header_logo{width:300px;display:flex;flex-direction:row;align-items:center;grid-gap:5px;gap:5px}@media screen and (max-width: 720px){.header_logo{grid-gap:0px;gap:0px}}.header_buttons{display:flex;flex-direction:row;grid-gap:10px;gap:10px;align-items:center}@media screen and (max-width: 720px){.header_buttons{justify-content:center}}.header_buttons .header_button{padding:8px;cursor:pointer;border-radius:5px;color:#310957;font-weight:bolder;background-color:#eee1ee}.header_buttons .header_button:hover{background:#eecdee}.header_buttons .header_button:active{outline:none;box-shadow:0px 0px 13px 4px #b274e6}@media screen and (max-width: 720px){.header_buttons .header_button{padding:4px}}.header_phrase{color:#eee1ee;font-size:14px;line-height:15px;font-weight:bold}@media screen and (max-width: 720px){.header_phrase{font-size:10px;line-height:10px;text-align:center}}
.wordCard{width:150px;height:200px;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:10px;gap:10px;background:linear-gradient(180deg, rgba(247, 234, 248, 0.7) 0%, rgba(239, 177, 255, 0.7) 99%);color:#310957;border-radius:10px}@media screen and (max-width: 380px){.wordCard{width:100px;height:150px}}.checking{display:flex;flex-direction:column;text-align:center;grid-gap:10px;gap:10px}.checking .checking_btn{padding:8px;border-radius:5px;border:1px solid #c168d3;font-size:10px;color:#c168d3;cursor:pointer}
.buttonCheck{margin-top:25px;padding:8px;font-family:"Roboto",sans-serif;border-radius:5px;background-color:#eee1ee;border:none;font-size:12px;cursor:pointer}.buttonCheck:focus{outline:2px dashed #473a95}@media screen and (max-width: 380px){.buttonCheck{font-size:10px}}
.cardsContainer{width:600px;height:400px;display:flex;flex-direction:column;align-items:center;justify-content:center;grid-gap:20px;gap:20px;margin-bottom:30px;border-radius:10px}@media screen and (max-width: 720px){.cardsContainer{width:300px}}@media screen and (max-width: 380px){.cardsContainer{width:200px;height:250px}}.cardsContainer_cards{display:flex;flex-direction:row;align-items:center;justify-content:center;grid-gap:20px;gap:20px}@media screen and (max-width: 380px){.cardsContainer_cards{grid-gap:10px;gap:10px}}.cardsContainer_count{text-align:center;color:#efb1ff}
.arrow{padding:10px;border:1px solid #efb1ff;color:#efb1ff;font-size:30px;cursor:pointer;border-radius:5px}@media screen and (max-width: 380px){.arrow{font-size:10px;padding:8px}}.disabled{border:1px solid gray;color:gray;cursor:not-allowed}
.serverError{display:flex;flex-direction:column;align-items:center;text-align:center;font-size:20px;color:#eee1ee}
.main_table{max-width:1000px;width:700px;overflow:auto}@media screen and (max-width: 720px){.main_table{width:100%}}.table{width:100%;border:none;margin-bottom:20px;border-collapse:separate}.table tr th:first-child,.table tr td:first-child{border-left:1px solid #eee1ee}.table tr th:last-child,.table tr td:last-child{border-right:1px solid #eee1ee}.table thead tr th:first-child{border-radius:10px 0 0 0}.table thead tr th:last-child{border-radius:0 10px 0 0}.table tbody td{text-align:left;border:none;padding:10px 15px;font-size:14px}@media screen and (max-width: 720px){.table tbody td{padding:10px 5px;font-size:11px}}.table tbody tr:nth-child(odd) td{background:rgba(142,107,152,.75);color:#eee1ee}.table tbody tr:nth-child(even) td{background:#eee1ee;color:#310957}.table tbody tr:last-child td{border-bottom:1px solid #eee1ee}.table tbody tr:last-child td:first-child{border-radius:0 0 0 10px}.table tbody tr:last-child td:last-child{border-radius:0 0 10px 0}
.table_header th{font-weight:bold;text-align:center;border:none;padding:10px 15px;background:#eee1ee;font-size:18px;border-top:1px solid #eee1ee;color:#310957}@media screen and (max-width: 720px){.table_header th{font-size:14px}}
.tableRow_actions{display:flex;flex-direction:row;justify-content:center;grid-gap:5px;gap:5px;border:none}@media screen and (max-width: 720px){.tableRow_actions{grid-gap:2px;gap:2px}}.inputTableRow{width:140px;border:1px solid #685096;border-radius:5px;outline:none;text-align:left;font-family:"Roboto",sans-serif;font-weight:bold}@media screen and (max-width: 720px){.inputTableRow{width:80px;font-size:11px}}.redInputTableRow{border:2px solid red}
.button_actions{padding-left:5px;padding-right:4px;border-radius:5px;font-size:15px;cursor:pointer}.button_actions:hover{box-shadow:0px 0px 5px 1px #483a97}@media screen and (max-width: 720px){.button_actions{font-size:10px}}.disabled_button{background:#f5e1e1;cursor:not-allowed;z-index:-1}
.spinner-eff{position:relative;width:50px;height:50px}.spinner-eff:before,.spinner-eff:after{content:"";display:block}.spinner-eff .spinner-bar:before,.spinner-eff .spinner-bar:after{content:"";display:block}@keyframes rotation-top{0%{transform:rotate(0deg);opacity:.5}20%{transform:rotate(360deg);opacity:1}80%{transform:rotate(360deg);opacity:1}100%{transform:rotate(360deg);opacity:.5}}@keyframes rotation-right{0%{transform:rotate(0deg);opacity:.5}20%{transform:rotate(0deg);opacity:.5}40%{transform:rotate(360deg);opacity:1}80%{transform:rotate(360deg);opacity:1}100%{transform:rotate(360deg);opacity:.5}}@keyframes rotation-bottom{0%{transform:rotate(0deg);opacity:.5}40%{transform:rotate(0deg);opacity:.5}60%{transform:rotate(360deg);opacity:1}80%{transform:rotate(360deg);opacity:1}100%{transform:rotate(360deg);opacity:.5}}@keyframes rotation-left{0%{transform:rotate(0deg);opacity:.5}60%{transform:rotate(0deg);opacity:.5}80%{transform:rotate(360deg);opacity:1}100%{transform:rotate(360deg);opacity:.5}}.spinner-eff.spinner-eff-1 .bar{width:0;height:0;border:25px solid transparent;position:absolute;top:0;left:0;transform:rotate(0deg);opacity:.5}.spinner-eff.spinner-eff-1 .bar-top{border-top:25px solid #47a8bd;animation:rotation-top 3.6s linear 0s infinite}.spinner-eff.spinner-eff-1 .bar-right{border-right:25px solid #47a8bd;animation:rotation-right 3.6s linear 0s infinite}.spinner-eff.spinner-eff-1 .bar-bottom{border-bottom:25px solid #47a8bd;animation:rotation-bottom 3.6s linear 0s infinite}.spinner-eff.spinner-eff-1 .bar-left{border-left:25px solid #47a8bd;animation:rotation-left 3.6s linear 0s infinite}.container{padding-top:100px}
.footer{display:flex;justify-content:space-evenly;align-items:center;padding-bottom:20px;color:#eee1ee}@media screen and (max-width: 720px){.footer{flex-direction:column;grid-gap:7px;gap:7px;padding-bottom:5px}}a{text-decoration:none}
.errorPage{display:flex;flex-direction:column;align-items:center;grid-gap:30px;gap:30px;padding-top:100px}.errorPage_text{font-size:20px;font-weight:bold;color:#eee1ee}.errorPic{width:400px}
.newWord{margin-top:10px;display:flex;flex-direction:column;padding:50px;grid-gap:5px;gap:5px;border:1px solid #eee1ee;border-radius:5px;color:#eee1ee}.newWord-title{text-align:center;font-size:20px;color:#eee1ee}
